(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Purchase12Controller', Purchase12Controller);

  /** @ngInject */
  function Purchase12Controller($state, PurchaseService, $http, configUrls, environment) {
    var vm = this;

    vm.payment = {};
    vm.schedule = ""

    vm.payment.name = "Taro Yamada"
    vm.payment.number = "---9282"
    vm.payment.expirym = "10"
    vm.payment.expiryy = "30"
    vm.schedule = "yearly"
    vm.button1 = "Confirm Payment" 

    vm.proceed = function() {
      vm.button1 = "Loading..."
      if(vm.schedule=="monthly") {
        PurchaseService.information2.Header.InstallmentPremium = PurchaseService.information2.Header.InstallmentPremium / 12;
        PurchaseService.information2.Header.PaymentFrequency = 12;
        PurchaseService.information2.Risks[0].RiskBody.PaymentFrequency = 12;
      } else if(vm.schedule=="yearly") {
        PurchaseService.information2.Header.PaymentFrequency = 1;
        PurchaseService.information2.Risks[0].RiskBody.PaymentFrequency = 1;
        PurchaseService.information2.Header.InstallmentPremium = PurchaseService.information2.Header.InstallmentPremium;
      }
      PurchaseService.information2.Header.Mandate.CreditCard.CardNumber = vm.payment.number
      PurchaseService.information2.Header.Mandate.CreditCard.ExpiryMonth = vm.payment.expirym
      PurchaseService.information2.Header.Mandate.CreditCard.ExpityYear = vm.payment.expiryy
      vm.btn1 ='Loading...';
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/newbusiness/newpolicy',
        url: configUrls[environment].createPolicy,
        data: angular.toJson(PurchaseService.information2)
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function (response) {
          var polNo = response.data.PolicyID
          PurchaseService.setPolicyNumber(polNo)
          $state.go('purchase-13')
        }, function () {
      });
    };
  }
})();

