(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Micro1Controller', Micro1Controller);

  /** @ngInject */
  function Micro1Controller(MicroService, $stateParams, $http, $state, configUrls, environment) {
    var vm = this;

    vm.policyTemplate = MicroService.policyTemplate
    vm.PolNo = MicroService.PolNo

    vm.policyNumber = '12312312'
    vm.policyCover = '$' + MicroService.Cover

    vm.loading = false
    vm.checkbox = false
    vm.loaded = false

    vm.loadPolicy = function (PolNo) {
      if(vm.checkbox) {
        if(PolNo) {
          vm.getPolicy(PolNo)
          console.log('load')
        } else {
          console.log('new')
          vm.newPolicy()
        }
      }
    }

    vm.newPolicy = function () {
      //vm.loading = true
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/newbusiness/newpolicy',
        url: configUrls[environment].createPolicy,
        data: angular.toJson(vm.policyTemplate)
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }

      $http(req)
        .then(function (response) {
          $state.go('micro-1', {'policyNumber': response.data.PolicyID});
        }, function () {
      });
    }

    vm.getPolicy = function (PolNo) {
      //vm.loading = true
      var req = {
        method: 'GET',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/enquiry/policy/' + PolNo
        url: configUrls[environment].enquirePolicy +  PolNo,
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function (response) {
          vm.loading = false
          vm.loaded = true
          vm.cover = response.data.Cont.Risks["1"].RiskBody.Options[0].SumInsured
        }, function () {
      });
    }

    function init() {
      if($stateParams.policyNumber=="new") {
        vm.checkbox = false
      } else if($stateParams.policyNumber) {
        MicroService.setPolNo($stateParams.policyNumber)
        vm.checkbox = true
        vm.PolNo = $stateParams.policyNumber
        vm.loadPolicy(vm.PolNo)
      }
    }

    init();

  }
})();
(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .service('PolicyService', PolicyService);

  /** @ngInject */
  function PolicyService() {
    var vm = this;

    vm.PlanName = ""

    vm.setPolicyDetails = function (policyDetails) {
      vm.policyDetails = policyDetails;
    };

    vm.setPolNo = function (polNo) {
      vm.polNo = polNo;
    };

    vm.setConfirmationMessage = function (ConfirmationMessage) {
      vm.ConfirmationMessage = ConfirmationMessage;
    };

    vm.mapPlanName = function (plan) {
      switch(plan) {
        case "Plan1":
          return "Basic"
        case "Plan2":
          return "Relief"
        case "Plan3":
          return "Complete"
      }
    }

    vm.cancellationBody = {
      "PolicyID": "070910",
      "TranDate": "2021-09-22T09:40:36.940641599Z",
      "CancelDate": "2021-09-22",
      "CancelBy": "Customer",
      "CancelReason":"Cancel Reason"
    }

    vm.mapInsuredTypeName = function () {

    }
    vm.numberWithCommas = function (x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  }
})();

