(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('PolicyDetailController', PolicyDetailController);

  /** @ngInject */
  function PolicyDetailController($state, PolicyService, $http, configUrls, environment) {
    var vm = this;

    vm.policyDetails = {};
    vm.cancellationBody = PolicyService.cancellationBody
    vm.panel1 = {};

    vm.polNo = PolicyService.polNo;

    vm.link1 = 'Edit'
    vm.plantype = '';

    vm.stopRenew = function () {
      vm.cancellationBody.PolicyID = PolicyService.polNo;
      vm.cancellationBody.TranDate = moment().format("YYYY-MM-DD");
      vm.cancellationBody.CancelDate  = moment(PolicyService.policyDetails.Cont.Risks["1"].Risk_End).format("YYYY-MM-DD");
      vm.cancellationBody.CancelReason = 'Cancellation of Renewal'
      vm.btn1 ='Loading';
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/service/cancelpolicy',
        url: configUrls[environment].cancelPolicy,
        data: angular.toJson(vm.cancellationBody)
      };

      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function () {
          PolicyService.setConfirmationMessage('' + moment(PolicyService.policyDetails.Cont.EndDate).format("YYYY-MM-DD") + '')
          $state.go('policy-confirmation')
        }, function () {
      });
    }

    function init() {
      if (!_.isEmpty(PolicyService.policyDetails)) {
        vm.policyDetails = PolicyService.policyDetails;

        vm.polNo = PolicyService.polNo;
        vm.plantype = PolicyService.PlanName;
        vm.price = PolicyService.numberWithCommas(PolicyService.policyDetails.Cont.Risks["1"].RiskBody.TotalPremium) + ''
        vm.panel1.price1 = PolicyService.numberWithCommas(PolicyService.policyDetails.Cont.Risks["1"].RiskBody.Benefit_Limits[0].PolicyLimit) + ''
        vm.panel1.price2 = PolicyService.numberWithCommas(PolicyService.policyDetails.Cont.Risks["1"].RiskBody.Benefit_Limits[2].DailyLimit) + ''
        vm.panel1.price4 = PolicyService.numberWithCommas(PolicyService.policyDetails.Cont.Risks["1"].RiskBody.Benefit_Limits[3].PolicyLimit) + ''
        vm.panel1.price5 = PolicyService.numberWithCommas(PolicyService.policyDetails.Cont.Risks["1"].RiskBody.Benefit_Limits[4].PolicyLimit) + ''
        vm.panel1.price6 = PolicyService.numberWithCommas(PolicyService.policyDetails.Cont.Risks["1"].RiskBody.Benefit_Limits[5].PolicyLimit) + ''

        vm.contractDate = moment(vm.policyDetails.Cont.StartDate).format("YYYY-MM-DD");
        vm.name = vm.policyDetails.Cont.Owner.Name;
        vm.dob = moment(vm.policyDetails.Cont.Owner.Dob).format("YYYY-MM-DD");
        vm.gender = vm.policyDetails.Cont.Owner.Gender;
        vm.pcode = vm.policyDetails.Cont.Owner.Contact.Add.Pcode.substring(0, 3) +'-'+ vm.policyDetails.Cont.Owner.Contact.Add.Pcode.substring(4, 8);
        vm.add = vm.policyDetails.Cont.Owner.Contact.Add.Add1;
        vm.email = vm.policyDetails.Cont.Owner.Contact.Email;
        vm.phone = vm.policyDetails.Cont.Owner.Contact.Phone;

        vm.insuredName = vm.policyDetails.Cont.Risks["1"].RiskBody.InsuredParty.Name;
        vm.insuredDob = moment(vm.policyDetails.Cont.Risks["1"].RiskBody.InsuredParty.Dob).format("YYYY-MM-DD");
        vm.insuredGender = vm.policyDetails.Cont.Risks["1"].RiskBody.InsuredParty.Gender
        vm.insuredKinship = vm.policyDetails.Cont.Risks["1"].RiskBody.InsuredParty.Kinship
        vm.paymentDetails = " -  -  - " + vm.policyDetails.Cont.Mandate.CreditCard.CardNumber.substring("15", "19")

        vm.nextRenewalDate = moment(PolicyService.policyDetails.Cont.EndDate).format("YYYY-MM-DD")

      } else if(_.isEmpty(PolicyService.policyDetails)) {
        $state.go('policy-login');
      }
    }
    init();
  }

})();

